/**affiche les informations d'un partenaire */
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import swal from 'sweetalert';
import classnames from 'classnames';
import PageLoader from '../../Common/PageLoader';
import {
    Row,
    Col,
    Progress,
    Card,
    CardBody,
    CardHeader,
    CardFooter,
    FormGroup,
    Input,
    TabContent,
    TabPane,
    Button,
    Nav,
    NavItem,
    NavLink, } 
from 'reactstrap';
import ContentWrapper from '../Layout/ContentWrapper';
import {url_partenaires} from '../../../urls'
import HeadingMenu from '../Layout/HeadingMenu';
import TicketPartenaire from './Ticket_Partenaire';

class ShowPartenaire extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activeStep:'1',
            progressValue: [100, 0, 0],
        };
    }

    toggleStep = activeStep => () => {
        const progessValue1 = [100, 0, 0];
        const progessValue2 = [0, 100, 0];
        const progessValue3 = [0, 0, 100];
        
        if (this.state.activeStep !== activeStep) {
            this.setState({
                activeStep : activeStep
            });
        }
        switch (activeStep) {
            case '1':
                this.setState({
                    progressValue: progessValue1
                });
                break;
            case '2':
                this.setState({
                    progressValue: progessValue2
                });
                break;
            case '3':
                this.setState({
                    progressValue: progessValue3
                });
                break;
            default:
                console.log('default')
        }
    }
    
    render() {
        const partenaire = this.props.partenaire
        const key = partenaire.key
        const level1 = this.state.progressValue[0]
        const level2 = this.state.progressValue[1]
        const level3 = this.state.progressValue[2]
        const colorTabHeader = "success"

        console.log(key)
        return ( 
          //  !isLoading? (
            <ContentWrapper>
                <div className="d-flex ml-auto col mb-4" hidden>
                    {/*updated date */}
                    <div className="mr-auto p-0" hidden>
                        <small className="mb-0 mt-1 text-dark">Mis à jour le : 04/05/21 14:24:18</small>
                    </div>
                </div>
                <Card className="card-default my-1">
                    <CardHeader className="pb-0">
                        <Row>
                            <div className="col pr-0" width="20%"> <Progress color={colorTabHeader} value={level1}/> </div>
                            <div className="col p-0"> <Progress color={colorTabHeader} value={level2}/> </div>
                            <div className="col pl-0"> <Progress color={colorTabHeader} value={level3}/> </div>
                        </Row>
                    </CardHeader>
                    <CardBody className="pt-0 pb-0 pl-14 pr-14">         
                        <Nav tabs justified >
                            <NavItem>
                                <NavLink
                                tag="div"
                                className={classnames( {active: this.state.activeStep === '1'} )}
                                onClick={this.toggleStep('1')}
                                >
                                    <h4 className="text-left my-2">Tickets</h4>
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink
                                tag="div"
                                className={classnames( {active: this.state.activeStep === '2'} )}
                                onClick={this.toggleStep('2')}
                                >
                                    <h4 className="text-left my-2">Publications</h4>
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink
                                tag="div"
                                className={classnames( {active: this.state.activeStep === '3'} )}
                                onClick={this.toggleStep('3')}
                                >
                                    <h4 className="text-left my-2">Statistiques</h4>
                                </NavLink>
                            </NavItem>
                        </Nav>
                    </CardBody>    
                    <TabContent activeTab={this.state.activeStep} className="pt-0">
                        {/*tickets */}
                        <TabPane id="tabPane1" tabId="1">
                            <TicketPartenaire id={key}/>
                            <div className="d-flex">
                                <Button
                                className="ml-auto"
                                color="success"
                                onClick={this.toggleStep('2')}
                                >
                                    Suivant
                                </Button>
                            </div>                    
                        </TabPane> 
                        {/*publication */}                        
                        <TabPane id="tabPane2" tabId="2">
                            {/*<ActivitePartenaire partenaire={this.state.partenaire}/>*/}
                            <div className="d-flex">
                                <Button outline color="primary" onClick={this.toggleStep('1')}>
                                    Precedent
                                </Button>
                                <Button
                                className="ml-auto"
                                color="success"
                                onClick={this.toggleStep('3')}
                                >
                                    Suivant
                                </Button>
                            </div>
                        </TabPane>
                        {/*statistiques */}
                        <TabPane id="tabPane3" tabId="3">      
                            {/*<StatPartenaire partenaire={this.state.partenaire}/>*/}
                            <div className="d-flex">
                                <Button outline color="primary" onClick={this.toggleStep('3')}>
                                    Precedent
                                </Button>
                                <Button 
                                className="ml-auto"
                                color="success"
                                >         
                                    Terminer
                                </Button>
                            </div>
                        </TabPane>
                    </TabContent>                                            
                </Card>
                <Row>

                </Row>
            </ContentWrapper>
         /*   ):(
                <PageLoader/>
            )*/
           
        );
    }
}


export default ShowPartenaire;