 import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';
import {url_categories} from '../../../urls'
import ContentWrapper from '../Layout/ContentWrapper';
import HeadingMenu from '../Layout/HeadingMenu';
import { Row, Col, Card, CardHeader, CardBody, Button } from 'reactstrap';
import swal from 'sweetalert';
import PageLoader from '../../Common/PageLoader';

const ApercuTicket = (props) => {
    const ticket = props.ticket
    const {titre, prix, qte, description, logo, isPopular, createdAt, partenaire, categorie} = ticket

    return(
        <Card className="b text-center">
            <span> {categorie.nom} </span>
            <hr/>
            <CardBody>
                {isPopular? (
                    <span className="ml-auto circle bg-success circle-lg"></span>
                ):( 
                    <span className="ml-auto circle bg-orange circle-lg"></span>
                )}
                <img className="rounded-circle thumb64 mb-2" src={logo} alt="logo"/>
                <p className="h4 text-bold mb-0">{titre}</p>
                <p>{description}</p>
                <br/>
                <h3> <em> {prix} </em> </h3>
                <Link to={``} className="btn btn-info btn-oval" type="button" hidden> 
                    Consulter 
                </Link>
            </CardBody>
            <CardBody className="bt">
                <Row>
                    <Col col="8" className="br">
                        <em className="fas fa-user-tie fa-fw text-primary mr-3"></em>
                        <strong> {categorie.nom} </strong>
                    </Col>
                    <Col col="4" className="br">
                        <em className="fas fa-ticket-alt fa-fw text-warning mr-3"></em>
                        <strong>+{qte} </strong>
                    </Col>
                </Row>
            </CardBody>
        </Card>    
    )
}

class TicketPartenaire extends Component {
    constructor() {
        super()
        this.state = {
            defaultTickets: [],
            tickets: [],
        }
    }

    getTicket = (id) => {
        let tickets = [];
        axios.get(url_categories)
        .then(res => {
            const response = res.data.data
            console.log(res.data.data)
            const tickets = []
            response.map((valeur) => {
                const _tickets = valeur.tickets
                if(_tickets.length) {
                    _tickets.map((ticket) => {
                        const {titre, prix, qte, validite, description, logo, isPopular, createdAt, partenaire} = valeur.tickets
                        console.log(partenaire)
                        const categorie = {}
                        categorie.nom = valeur.nom
                        categorie.description = valeur.description
                        categorie.logo = valeur.logo

                        if(partenaire.key == id) {
                            tickets.push({
                                titre,
                                description,
                                logo,
                                qte,
                                prix,
                                validite,
                                description,
                                isPopular,
                                createdAt,
                                categorie,
                            })
                        }
                    })
                    
                }
                
                
            })
            this.setState({
                defaultTickets: tickets,
                tickets, 
                isLoading: false,
            })
            console.log(this.state.defaultTickets);
        })
        .catch((error) => {
            console.log("get tickets failed!", error)
            swal('Erreur', error.message, 'error')
        })
    }

    componentDidMount() {
        console.log(this.props)
        const id  = 'GlAcjsHBpCLncH6r1N8R'       
        this.getTicket(id);
    }

    render() {
       const tickets = this.state.defaultTickets
       const isLoading = this.state.isLoading
       /* with no connection
       const services = [
           {
               key: "test001",
               nom: "Carburant",
               description: "Faites le plein en energie verte!",
               logo: "img/user/02.jpg",
               partenaires: [{
                   key: "pat001",
                   nom: "Orange Cameroun",
                   description: "La vie change avec Orange",
                   logo: "orange.png"
               }],
               tickets: [{
                   id_ticket: '1',
                   prix: 3,
                   quantite: 5,
               }],
               nb_partenaire: 1,
               nb_ticket: 1,
               nb_bte: 1500 
           }
       ]*/
        return (
            !isLoading? (
            <ContentWrapper>
                <Row>
                    <div className="col-xl-3 ml-auto">
                            {/*add icon */}
                            <Link to={`/add_service/`} title="Ajouter un service" className="d-flex col-xl-7 ml-auto pr-3">
                                <Button color="success"> Ajouter </Button>
                            </Link>
                    </div>
                </Row>
                <Row className="mr-15">
                    <Col>
                        <Row>
                            {tickets.map((ticket) => {
                                return (
                                    <Col xl="2" lg="6">
                                        <ApercuTicket ticket = {ticket}/>
                                    </Col>
                                )
                            })}
                        </Row>
                    </Col>
                </Row>
            </ContentWrapper>
            ):(
                <PageLoader/>
            ));
    }

}

export default TicketPartenaire;
